import xhr from '../utils/xhrutils';
import { M, log } from '../utils/webutils';

log.config('console', 'basic', 'ALL', 'webpay-renderer');

const pspid = 'AppApiClient';
const path = '/api';

export default {
  request(action, options) {
    const uri = path + action;
    switch(action) {
      case '/payment':
        return new Promise((resolve, reject) => {
          paypal.Button.render({
            env: 'sandbox'
            , commit: true
            , style: {color: 'gold', size: 'small'}
            , client: {
              sandbox:    'PAYPAL_SANDBOX_CLIENT_ID',
              production: 'PAYPAL_PRODUCTION_CLIENT_ID'
            }
            , payment: function(data, actions) {
              return actions.payment.create({
                payment: { transactions: [
                  { amount: { total: '1.00', currency: 'USD' }}
                ]}
              });
            }
            , onAuthorize: function(data, actions) {
              resolve({message: 'Execute the payment.'});
            }
            , onCancel: function(data, actions) {
              resolve({message: 'Buyer cancelled the payment.'});
            }
            , onError: function(err) {
              if(reject) reject(err);
              resolve(
                {message: 'An error occurred during the transaction'});
            }
          }, '#paypal-button');
        });
      case '/currency':
        return new Promise((resolve, reject) => {
          xhr.get(uri, options
            , items => { resolve(items); }
            , error => { if(reject) reject(error); } 
          );
        });
      default:
        return new Promise((resolve, reject) => {
          if(reject) reject({error: {message: 'Unknown Operation.'}});
        });
    }
  },
  getPayment(options) {
    return this.request('/payment', options);
  },
  getCurrency(options) {
    return this.request('/currency', options);
  },
  fetchPayment(options) {
    return this.getPayment(options)
      .then(R.tap(this.logTrace.bind(this)))
      .catch(this.logError);
  },
  fetchCurrency(options) {
    return this.getCurrency(options)
      .then(R.tap(this.logTrace.bind(this)))
      .catch(this.logError);
  },
  logTrace(message) {
    log.trace(`${pspid}>`, 'Response:', message)
  },
  logError(message) {
    log.error(`${pspid}>`, 'Error   :', message)
  },
}



